import { FC } from "react";
import classNames from "classnames/bind";

import { Link, LinkType } from "common/components/Link";
import { CountryVersion, LogoTheme, Theme } from "common/constants/enum";
import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import { Icon } from "common/components/Icons";
import LangIcon from "common/components/Icons/icons/24/lang.svg";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import LogoMonoIcon from "common/components/Icons/icons/logo-mono.svg";
import OrthosLogo from "common/components/Icons/icons/orthos-logo.svg";
import SmilikeLogo from "common/components/Icons/icons/smilike-logo.svg";
import CovidIcon from "common/components/Icons/icons/24/covid-shield.svg";
import PhoneIcon from "common/components/Icons/icons/24/phone.svg";
import MailIcon from "common/components/Icons/icons/24/mail.svg";
import TiktokIcon from "common/components/Icons/icons/24/tiktok.svg";
import WhatsappIcon from "common/components/Icons/icons/24/whatsapp.svg";
import InstagramIcon from "common/components/Icons/icons/24/instagram.svg";

import { FooterProps } from "./Footer.types";
import styles from "./Footer.module.scss";

const cx = classNames.bind(styles);

export const Footer: FC<FooterProps> = ({
    wa,
    site,
    email,
    phone,
    covid,
    theme = Theme.DARK,
    logoTheme = LogoTheme.BICOLOR,
    tiktok,
    instagram,
    isMailPage,
    privacyPolicy,
    version,
}) => {
    let Logo = logoTheme === LogoTheme.BICOLOR ? LogoIcon : LogoMonoIcon;

    if (version === CountryVersion.UKRAINE) {
        Logo = OrthosLogo;
    }

    if (version === CountryVersion.GERMANY) {
        Logo = SmilikeLogo;
    }

    return (
        <footer
            className={cx("root", {
                root_light: theme === Theme.LIGHT,
                "root_type-germany": version === CountryVersion.GERMANY,
            })}
        >
            {isMailPage && (
                <div className={cx("logo")}>
                    <Icon className={cx("logo__icon")}>
                        <Logo />
                    </Icon>
                </div>
            )}
            {!isMailPage && (
                <>
                    <Link
                        id={getAnalyticsId({
                            zone: "footer",
                            element: GetAnalyticsIdElement.LINK,
                            label: "logo",
                        })}
                        {...site}
                        className={cx("logo")}
                        type={LinkType.withoutName}
                    >
                        <Icon className={cx("logo__icon")}>
                            <Logo />
                        </Icon>
                    </Link>
                    <Link
                        id={getAnalyticsId({
                            zone: "footer",
                            element: GetAnalyticsIdElement.LINK,
                            label: "privacyPolicy",
                        })}
                        {...privacyPolicy}
                        target="_blank"
                        className={cx("privacy")}
                    />
                    <div className={cx("container-links")}>
                        {tiktok && (
                            <Link
                                id={getAnalyticsId({
                                    zone: "footer",
                                    element: GetAnalyticsIdElement.LINK,
                                    label: "tiktok",
                                })}
                                {...tiktok}
                                className={cx("link")}
                            >
                                {tiktok.name}
                                <Icon className={cx("link__icon")}>
                                    <TiktokIcon />
                                </Icon>
                            </Link>
                        )}
                        {instagram && (
                            <Link
                                id={getAnalyticsId({
                                    zone: "footer",
                                    element: GetAnalyticsIdElement.LINK,
                                    label: "instagram",
                                })}
                                {...instagram}
                                className={cx("link")}
                            >
                                {instagram.name}
                                <Icon className={cx("link__icon")}>
                                    <InstagramIcon />
                                </Icon>
                            </Link>
                        )}
                        {phone && (
                            <Link
                                id={getAnalyticsId({
                                    zone: "footer",
                                    element: GetAnalyticsIdElement.LINK,
                                    label: "phone",
                                })}
                                {...phone}
                                className={cx("link", "link_phone")}
                            >
                                {phone.name}
                                <Icon className={cx("link__icon")}>
                                    <PhoneIcon />
                                </Icon>
                            </Link>
                        )}
                        <Link
                            id={getAnalyticsId({
                                zone: "footer",
                                element: GetAnalyticsIdElement.LINK,
                                label: "wa",
                            })}
                            {...wa}
                            className={cx("link", "link_wa")}
                        >
                            {wa.name}
                            <Icon className={cx("link__icon")}>
                                <WhatsappIcon />
                            </Icon>
                        </Link>
                        <Link
                            id={getAnalyticsId({
                                zone: "footer",
                                element: GetAnalyticsIdElement.LINK,
                                label: "email",
                            })}
                            {...email}
                            className={cx("link", "link_email")}
                        >
                            {email.name}
                            <Icon className={cx("link__icon")}>
                                <MailIcon />
                            </Icon>
                        </Link>
                        <Link
                            id={getAnalyticsId({
                                zone: "footer",
                                element: GetAnalyticsIdElement.LINK,
                                label: "web",
                            })}
                            {...site}
                            className={cx("link", "link_site")}
                        >
                            {site.name}
                            <Icon className={cx("link__icon")}>
                                <LangIcon />
                            </Icon>
                        </Link>
                        {covid && (
                            <Link
                                id={getAnalyticsId({
                                    zone: "footer",
                                    element: GetAnalyticsIdElement.LINK,
                                    label: "covid",
                                })}
                                {...covid}
                                target="_blank"
                                className={cx("link", "link_covid")}
                            >
                                {covid.name}
                                <Icon className={cx("link__icon")}>
                                    <CovidIcon />
                                </Icon>
                            </Link>
                        )}
                    </div>
                </>
            )}
        </footer>
    );
};
