import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";

import { OldPriceProps, OldPriceTheme } from "./OldPrice.types";
import styles from "./OldPrice.module.scss";

const cx = classNames.bind(styles);

export const OldPrice: FunctionComponent<OldPriceProps> = ({
    content,
    theme = OldPriceTheme.WHITE,
    className,
}) => {
    if (!content) {
        return null;
    }

    return (
        <div>
            <Text className={cx("root", `root_${theme}`, className)}>
                {content}
            </Text>
        </div>
    );
};
